// src/pages/profile.js
import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import { Loader2, User, Mail, Home, Trash2, PlusCircle, Save, Phone, MapPin } from 'lucide-react';
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Separator } from "@/components/ui/separator";
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog";

// Removed ADDRESS_LABELS constant

// Helper function to decode user info (simple version)
function getUserInfoFromToken() {
    if (typeof window === 'undefined') return { isLoggedIn: false };
    const token = localStorage.getItem("token");
    if (!token) return { isLoggedIn: false };
    try {
        JSON.parse(atob(token.split(".")[1]));
        return { isLoggedIn: true };
    } catch (e) {
        return { isLoggedIn: false };
    }
}

export default function ProfilePage() {
    const router = useRouter();
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);
    const [isSaving, setIsSaving] = useState(false);
    const [error, setError] = useState(null);
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [addresses, setAddresses] = useState([]);
    
    // --- MODAL STATE ---
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [editingAddress, setEditingAddress] = useState(null); 
    const [editingIndex, setEditingIndex] = useState(-1);
    // -------------------

    const [sessionUser] = useState(getUserInfoFromToken());

    useEffect(() => {
        if (!sessionUser.isLoggedIn) {
            router.replace('/login');
            return;
        }

        async function fetchProfile() {
            try {
                const token = localStorage.getItem("token");
                const res = await fetch('/api/user/profile', {
                    headers: { 'Authorization': `Bearer ${token}` }
                });
                if (!res.ok) throw new Error('Failed to fetch profile data.');
                const { user: userData } = await res.json();
                
                setUser(userData);
                setName(userData.name || '');
                setPhone(userData.phone || ''); 
                setAddresses(userData.addresses || []);

            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        }
        fetchProfile();
    }, [sessionUser.isLoggedIn, router]);

    const handleSaveProfile = async (e) => {
        e.preventDefault();
        setIsSaving(true);
        setError(null);
        try {
            const token = localStorage.getItem("token");
            const res = await fetch('/api/user/profile', {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` },
                // Send phone in payload
                body: JSON.stringify({ name, phone, addresses }), 
            });

            if (!res.ok) {
                const errData = await res.json();
                throw new Error(errData.error || 'Failed to update profile.');
            }
            
            const { user: updatedUser } = await res.json();
            setUser(updatedUser);
            setPhone(updatedUser.phone);
            setAddresses(updatedUser.addresses);
            alert('Profile updated successfully! âœ…');
        } catch (err) {
            setError(err.message);
        } finally {
            setIsSaving(false);
        }
    };

    const handleAddressSave = (newAddress) => {
        const newAddresses = [...addresses];
        if (editingIndex > -1) {
            // Edit existing
            newAddresses[editingIndex] = newAddress;
        } else {
            // Add new
            newAddresses.push(newAddress);
        }
        setAddresses(newAddresses);
        setIsModalOpen(false);
    };

    const handleAddAddress = () => {
        setEditingAddress({
            label: "Home", 
            addressLine1: "", 
            city: "", 
            state: "", 
            pincode: "", 
            phone: ""
        });
        setEditingIndex(-1); // Index of -1 means adding a new address
        setIsModalOpen(true);
    };
    
    const handleEditAddress = (addr, index) => {
        setEditingAddress(addr);
        setEditingIndex(index);
        setIsModalOpen(true);
    };

    const removeAddress = (index) => {
        if (window.confirm("Are you sure you want to remove this address?")) {
             setAddresses(addresses.filter((_, i) => i !== index));
        }
    };

    if (loading) {
        return (
            <div className="flex justify-center items-center h-screen">
                <Loader2 className="h-8 w-8 animate-spin" />
            </div>
        );
    }
    
    if (!user) {
         return (
            <div className="p-10">
                <Alert variant="destructive">
                    <AlertTitle>Error</AlertTitle>
                    <AlertDescription>Could not load user profile.</AlertDescription>
                </Alert>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50/50 p-6 md:p-10">
            <div className="max-w-4xl mx-auto">
                <h1 className="text-3xl font-bold mb-6 flex items-center">
                    <User className="mr-3 h-6 w-6" /> My Profile
                </h1>

                {error && (
                    <Alert variant="destructive" className="mb-6">
                        <AlertTitle>Error</AlertTitle>
                        <AlertDescription>{error}</AlertDescription>
                    </Alert>
                )}

                <form onSubmit={handleSaveProfile} className="space-y-6">
                    {/* --- Basic Info Card --- */}
                    <Card>
                        <CardHeader>
                            <CardTitle>Basic Information</CardTitle>
                            <CardDescription>Update your personal details.</CardDescription>
                        </CardHeader>
                        <CardContent className="space-y-4">
                            <div className="space-y-2">
                                <Label htmlFor="name">Name</Label>
                                <Input id="name" value={name} onChange={(e) => setName(e.target.value)} required />
                            </div>
                            <div className="space-y-2">
                                <Label htmlFor="phone">Phone Number</Label>
                                <div className="flex items-center text-muted-foreground">
                                    <Phone className="h-4 w-4 mr-2" />
                                    <Input id="phone" value={phone} onChange={(e) => setPhone(e.target.value)} type="tel" placeholder="Your contact number" />
                                </div>
                            </div>
                            <div className="space-y-2">
                                <Label htmlFor="email">Email</Label>
                                <div className="flex items-center text-muted-foreground">
                                    <Mail className="h-4 w-4 mr-2" />
                                    <Input id="email" value={user.email} disabled className="bg-gray-100" />
                                </div>
                                <p className="text-xs text-muted-foreground">Email address cannot be changed.</p>
                            </div>
                        </CardContent>
                    </Card>

                    {/* --- Addresses Card --- */}
                    <Card>
                        <CardHeader>
                            <CardTitle className="flex items-center justify-between">
                                Shipping Addresses
                                <Button type="button" onClick={handleAddAddress} variant="secondary" size="sm">
                                    <PlusCircle className="h-4 w-4 mr-2" /> Add Address
                                </Button>
                            </CardTitle>
                            <CardDescription>Manage your saved delivery and billing addresses.</CardDescription>
                        </CardHeader>
                        <CardContent className="space-y-4">
                            {addresses.length === 0 && <p className="text-muted-foreground italic">No addresses saved.</p>}

                            {addresses.map((addr, index) => (
                                <div key={addr._id || index} className="border p-4 rounded-lg bg-white space-y-3 flex justify-between items-center">
                                    <div>
                                        <div className="font-bold text-base flex items-center">
                                            <MapPin className="h-4 w-4 mr-2 text-primary" />
                                            {addr.label || 'Other'}
                                        </div>
                                        <p className="text-sm text-gray-700 mt-1">
                                            {addr.addressLine1}, {addr.city} - {addr.pincode}
                                        </p>
                                        <p className="text-xs text-muted-foreground">
                                            Contact: {addr.phone || 'N/A'}
                                        </p>
                                    </div>
                                    <div className="flex space-x-2">
                                        <Button 
                                            type="button" 
                                            onClick={() => handleEditAddress(addr, index)} 
                                            variant="outline" 
                                            size="sm"
                                        >
                                            Edit
                                        </Button>
                                        <Button type="button" onClick={() => removeAddress(index)} variant="ghost" size="icon">
                                            <Trash2 className="h-4 w-4 text-red-500" />
                                        </Button>
                                    </div>
                                </div>
                            ))}
                        </CardContent>
                    </Card>

                    {/* --- Save Button --- */}
                    <div className="flex justify-end">
                        <Button type="submit" disabled={isSaving}>
                            {isSaving ? <Loader2 className="h-4 w-4 mr-2 animate-spin" /> : <Save className="h-4 w-4 mr-2" />}
                            Save Changes
                        </Button>
                    </div>
                </form>
                
                {/* --- Address Modal Component (Pop-up UI) --- */}
                <AddressModal
                    isOpen={isModalOpen}
                    onClose={() => setIsModalOpen(false)}
                    onSave={handleAddressSave}
                    initialAddress={editingAddress}
                    isNew={editingIndex === -1}
                />
            </div>
        </div>
    );
}

// --- UPDATED COMPONENT: Address Modal UI ---
function AddressModal({ isOpen, onClose, onSave, initialAddress, isNew }) {
    // Note: initialAddress must contain a `label` property, defaulted to "Home" in parent component.
    const [address, setAddress] = useState(initialAddress || {});

    useEffect(() => {
        // Reset state when modal opens/changes context
        setAddress(initialAddress || {
            label: "Home", addressLine1: "", city: "", state: "", pincode: "", phone: ""
        });
    }, [initialAddress]);

    const handleChange = (field, value) => {
        setAddress(prev => ({ ...prev, [field]: value }));
    };

    const handleSave = (e) => {
        e.preventDefault();
        if (!address.addressLine1 || !address.city || !address.phone) {
            alert("Street Address, City, and Phone are required.");
            return;
        }
        // Ensure the new address object structure is preserved
        onSave(address);
    };

    if (!address) return null;

    return (
        <Dialog open={isOpen} onOpenChange={onClose}>
            <DialogContent className="sm:max-w-[425px]">
                <DialogHeader>
                    <DialogTitle>{isNew ? "Add New Address" : "Edit Address"}</DialogTitle>
                    <DialogDescription>
                        Provide the complete details for this shipping location.
                    </DialogDescription>
                </DialogHeader>
                <form onSubmit={handleSave} className="grid gap-4 py-4">
                    {/* --- CHANGED: Input field instead of select for Label --- */}
                    <div className="space-y-2">
                        <Label htmlFor="label">Address Label (e.g., Home, Work, Friend's Place)</Label>
                        <Input 
                            id="label" 
                            value={address.label || ''} 
                            onChange={(e) => handleChange('label', e.target.value)}
                            placeholder="Home"
                            required
                        />
                    </div>
                    {/* --- END CHANGED --- */}
                    <div className="space-y-2">
                        <Label htmlFor="phone">Phone</Label>
                        <Input 
                            id="phone" 
                            value={address.phone || ''} 
                            onChange={(e) => handleChange('phone', e.target.value)}
                            placeholder="Contact phone number"
                            type="tel"
                            required
                        />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="addressLine1">Street Address</Label>
                        <Input 
                            id="addressLine1" 
                            value={address.addressLine1 || ''} 
                            onChange={(e) => handleChange('addressLine1', e.target.value)}
                            placeholder="House No., Street Name"
                            required
                        />
                    </div>
                    <div className="grid grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <Label htmlFor="city">City</Label>
                            <Input 
                                id="city" 
                                value={address.city || ''} 
                                onChange={(e) => handleChange('city', e.target.value)}
                                required
                            />
                        </div>
                        <div className="space-y-2">
                            <Label htmlFor="state">State</Label>
                            <Input 
                                id="state" 
                                value={address.state || ''} 
                                onChange={(e) => handleChange('state', e.target.value)}
                            />
                        </div>
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="pincode">Pincode</Label>
                        <Input 
                            id="pincode" 
                            value={address.pincode || ''} 
                            onChange={(e) => handleChange('pincode', e.target.value)}
                        />
                    </div>
                    
                    <DialogFooter>
                        <Button type="button" variant="outline" onClick={onClose}>Cancel</Button>
                        <Button type="submit">
                            <Save className="h-4 w-4 mr-2" /> {isNew ? "Add Address" : "Save Address"}
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}